import moment from "moment";
import { DATE_FORMAT } from "./constants";
export const isValidPortfolioData = (data) => {
    return Boolean(data.deposit ||
        data.income ||
        data.interest ||
        data.value ||
        data.withdrawal);
};
export const getDate = (date) => {
    return moment(date.slice(0, 10), DATE_FORMAT);
};
export const getCurrencyInCAD = (date, value, currencyCache) => {
    const multiplier = currencyCache[date.format(DATE_FORMAT)];
    return multiplier ? value / multiplier : value;
};
export const formatMoney = (amount) => {
    if (!amount) {
        return "N/A";
    }
    return amount.toLocaleString();
};
export const getSymbol = (security) => {
    return `${security.symbol || security.name}${security.currency === "usd" ? "" : ".TO"}`;
};
export const min = (data, field) => {
    return data.reduce((min, p) => (p[field] < min[field] ? p : min), data[0]);
};
export const max = (data, field) => {
    return data.reduce((max, p) => (p[field] > max[field] ? p : max), data[0]);
};
export const formatCurrency = (amount, digits) => {
    var si = [
        { value: 1, symbol: "" },
        { value: 1e3, symbol: "K" },
        { value: 1e6, symbol: "M" },
        { value: 1e9, symbol: "G" },
        { value: 1e12, symbol: "T" },
        { value: 1e15, symbol: "P" },
        { value: 1e18, symbol: "E" },
    ];
    var rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
    var i;
    for (i = si.length - 1; i > 0; i--) {
        if (Math.abs(amount) >= si[i].value) {
            break;
        }
    }
    const formattedAmount = (Math.abs(amount) / si[i].value).toFixed(digits).replace(rx, "$1") +
        si[i].symbol;
    return amount < 0 ? `-${formattedAmount}` : formattedAmount;
};
export function getPreviousWeekday(date) {
    const referenceDate = moment(date);
    let day = referenceDate.day();
    let diff = 1; // returns yesterday
    if (day === 0 || day === 1) {
        // is Sunday or Monday
        diff = day + 2; // returns Friday
    }
    return referenceDate.subtract(diff, "days");
}
